import {
  checkIfObject,
  searchInObjectArray,
} from 'src/assets/scripts/functions';
import FileUploader from 'src/components/Helpers/FilesUploader.vue';
import RenameFile from 'src/components/Dialogs/RenameFile.vue';
const { humanStorageSize } = format;
import { FileUploader_Mixin } from 'src/mixins';
import fnStore from 'stores/fn.store';
import { Helpers_Mixin } from 'src/mixins';

export default defineComponent({
  name: 'FilesPage',

  components: { CardIconBox, FileUploader, RenameFile },

  mixins: [
    // Uploader functions
    FileUploader_Mixin,
    Helpers_Mixin,
  ],

  data() {
    return {
      title: 'Files and Assets',
      searchQuery: '',
      ownerFilter: '',
      showLightBox: false,
      lightboxIndex: 0,
      lightboxImages: [],
      tableLoading: false,
      removedFile: null,
      selectedFiles: [],
      visibleColumns: [],
      onRequestDelayed: null,
      orgFiles: [],
      fileToRename: null,
      openRenameDialog: false,
      fileToDelete: null,
      openDeleteDialog: false,
      openMultiDeleteDialog: false,
      pagination: {
        descending: true,
        page: 1,
        rowsPerPage: 0,
        rowsNumber: 0,
      },
      columns: [
        {
          name: 'file',
          required: true,
          label: 'Name',
          align: 'left',
          field: (row) => (row.folder ? row.name : row.file),
          format: (val) => `${val}`,
          sortable: true,
        },
        {
          name: 'size',
          label: 'File size',
          align: 'left',
          field: (row) => (isNaN(row.size) ? '~' : row.size),
          format: (val) =>
            val === '~' ? '~' : `${humanStorageSize(Number(val || 0))}`,
          sortable: true,
          sort: (a, b) => parseInt(a, 10) - parseInt(b, 10),
        },
        {
          name: 'created_at',
          label: 'Date uploaded',
          align: 'left',
          field: (row) => row.created_at || '~',
          format: (val) =>
            val === '~' ? '~' : `${date.formatDate(val, 'MMM D, YYYY')}`,
          sortable: true,
          sort: (a, b) => parseInt(a, 10) - parseInt(b, 10),
        },
        {
          name: 'updated_at',
          label: 'Last updated',
          align: 'left',
          field: (row) => row.updated_at || '~',
          format: (val) =>
            val === '~' ? '~' : `${date.formatDate(val, 'MMM D, YYYY')}`,
          sortable: true,
          sort: (a, b) => parseInt(a, 10) - parseInt(b, 10),
        },
        {
          name: 'uploader_id',
          label: 'Uploaded by',
          align: 'left',
          field: (row) => row.uploader_id || '~',
          format: (val) =>
            val === '~'
              ? '~'
              : val === this.user.id
              ? 'me'
              : this.selectedOrgUsers?.[val]
              ? `${this.selectedOrgUsers?.[val]?.firstname} ${this.selectedOrgUsers?.[val]?.lastname}`
              : 'unknown',
          sortable: true,
          sort: (a, b) => parseInt(a, 10) - parseInt(b, 10),
        },
      ],
      orgFilesOriginal: {},
      path: '/',
      openURL,
    };
  },

  async mounted() {
    this.$root.setMeta({ title: this.title });
    this.visibleColumns = this.columns
      .map((c) => c.name) // Get all the column names
      .filter((e) => !['id'].includes(e)); // Hide some columns initially

    this.onRequestDelayed = debounce(this.onRequest, 1000);
    this.onRequest({
      pagination: this.pagination,
      filter: this.filter,
    });
    // Direct Link to Path
    if (this.route_path) this.path = this.route_path;
  },

  computed: {
    ...mapState(useUser, [
      'user',
      'selectedOrg',
      'selectedOrgFiles',
      'selectedOrgUsers',
      'selectedOrgBrands',
    ]),

    route_path() {
      return this.$route.query.path || '';
    },

    split_path() {
      return this.path.split('/');
    },

    location() {
      return window.location;
    },
  },

  watch: {
    searchQuery: {
      async handler(val) {
        this.onRequest(
          {
            pagination: this.pagination,
            filter: { search: val, owner: this.ownerFilter },
          },
          1000
        );
      },
    },
    ownerFilter: {
      async handler(val) {
        this.onRequest(
          {
            pagination: this.pagination,
            filter: { search: this.searchQuery, owner: this.ownerFilter },
          },
          1000
        );
      },
    },
    path: {
      async handler(val) {
        this.selectedFiles = [];
        this.$router.push({
          query: { path: val },
          params: { savePosition: true },
        });
        this.onRequest(
          {
            pagination: this.pagination,
            filter: { search: this.searchQuery, owner: this.ownerFilter },
          },
          1000
        );
      },
    },
    route_path: {
      async handler(val) {
        if (val !== this.path && this.$route.path == '/files') this.path = val;
      },
    },
    selectedFiles: {
      async handler(val) {
        if (val.filter((e) => e.folder)?.length) {
          this.selectedFiles = val.filter((e) => !e.folder);
        }
      },
    },
  },

  methods: {
    ...mapActions(useHelpers, ['uploadFile', 'getFiles']),

    generatePath(path_name, path_index) {
      if (!path_name && path_index === 0)
        return { path_query: '/', name: 'Organization Files' };
      const paths = this.split_path;
      const result = {
        name: path_name,
        path_query: '/' + paths.slice(1, path_index + 1).join('/'),
      };
      if (this.isPathOnBrands(paths.slice(0, path_index).join('/'))) {
        const brand = this.selectedOrgBrands[path_name];
        if (brand?.id && brand?.brand_name) {
          result.brand_id = brand.id;
          result.name = brand.brand_name;
        }
      }
      return result;
    },

    downloadZip(src_file, download_filename) {
      const to_download = src_file?.length ? src_file : this.selectedFiles;
      const label = download_filename || 'download';
      const files = [];
      for (const file of to_download) {
        if (!file.folder) {
          files.push({
            url: `${this.$cdn_host}/${file.path}`,
            filename: file.file,
          });
        }
      }
      this.$downloadZip(files, label);
    },

    isPathOnBrands(path) {
      if (path === null || path === undefined) path = this.path;
      return !path || path == '/';
    },

    openUploader() {
      try {
        const upModel = this.$refs['filesUploader'];
        upModel.$el.querySelector('.q-uploader__header').firstChild.click();
      } catch (e) {
        console.warn('File uploader error:', e.message);
      }
    },

    factoryFn(files) {
      let target_path = this.path;
      if (target_path.startsWith('/'))
        target_path = target_path.substring(1, target_path.length);
      if (target_path.endsWith('/'))
        target_path = target_path.substring(0, target_path.length - 1);
      return new Promise((resolve, reject) => {
        const token = fnStore.getAuth();
        resolve({
          method: 'POST',
          fieldName: 'uploads[0]',
          url: `${this.$config?.api?.baseURL}/file/organizations/${this.selectedOrg.id}`,
          formFields: [{ name: 'target_path', value: target_path }],
          headers: [{ name: 'Authorization', value: `Bearer ${token}` }],
          withCredentials: false,
        });
      });
    },

    updateLightBox() {
      setTimeout(() => {
        if (process.env.DEV_MODE) console.log('Updating Lightbox');
        this.lightboxImages = [];
        const images = this.orgFiles.filter((e) =>
          this.checkFileExtensions(e.file, [
            'ico',
            'jpg',
            'jpeg',
            'png',
            'svg',
            'gif',
          ])
        );
        images.forEach((img, i) => {
          this.lightboxImages.push({
            src: `${this.$cdn_host}/${img.path}`,
            title: img.file,
            alt: img.file,
          });
          const el = document.querySelector(`[file-id="${img.id}"]`);
          if (el)
            el.onclick = () => {
              if (el.attributes?.['file-id']?.value == img.id) {
                this.lightboxIndex = i;
                this.showLightBox = true;
              }
            };
        });
      }, 500);
    },

    dirBack1(path) {
      let newPath = (path || this.path || '').split('/');
      newPath.pop();
      return newPath.join('/');
    },

    fileRenameUpdate(fileInfo) {
      const indexToRename = this.orgFiles.findIndex((e) => e.id == fileInfo.id);
      if (indexToRename > -1) this.orgFiles[indexToRename] = fileInfo;
      this.updateLightBox();
    },

    async deleteSelectedFiles() {
      if (this.selectedFiles?.length) {
        for (const file of this.selectedFiles) {
          this.deleteFilesLocally(file);
        }
      }
    },

    async deleteFilesLocally(file) {
      if (file?.file_info) {
        this.$refs?.['filesUploader']?.removeFile?.(file?.file_info);
        this.removedFile = file?.file_info;
      }
      this.selectedFiles = this.selectedFiles.filter((e) => e.id != file.id);
      let indexToDelete = -1;
      if (!isNaN(file?.id) && (await this.deleteAction(file))) {
        indexToDelete = this.orgFiles.findIndex((e) => e.id == file.id);
      } else {
        indexToDelete = this.orgFiles.findIndex(
          (e) => e.file_info?.__key === file.file_info?.__key
        );
      }
      if (indexToDelete !== -1) this.orgFiles.splice(indexToDelete, 1);
      this.updateLightBox();
    },

    uploadedEvent({ files, xhr }) {
      const response = JSON.parse(xhr.response || '{}');
      if (response.success) {
        files.forEach((file, i) => {
          const fileIndex = this.orgFiles.findIndex(
            (e) => e.file_info?.__key === file.__key
          );
          if (fileIndex !== -1) {
            const last_upload = response.data[i];
            const totalDir = this.orgFiles.filter((e) => e.folder).length;
            this.orgFiles.splice(fileIndex, 1, last_upload);
          }
        });
      }
      this.updateLightBox();
    },

    failedEvent({ files, xhr }) {
      const response = JSON.parse(xhr.response || '{}');
      if (!response.success) {
        files.forEach((file) => {
          const fileIndex = this.orgFiles.findIndex(
            (e) => e.file_info?.__key === file.__key
          );
          if (fileIndex !== -1) {
            this.orgFiles[fileIndex].failed = true;
            this.orgFiles[fileIndex].uploading = false;
          }
        });
      }
    },

    filesAddedQued(files) {
      const totalDir = this.orgFiles.filter((e) => e.folder).length;
      files.forEach((file) => {
        let file_id = uid();
        let created_at = new Date();
        this.orgFiles.splice(totalDir, 0, {
          id: file_id,
          uid: file_id,
          file: file.name,
          folder: false,
          uploading: true,
          size: file.size,
          path: file.__img?.src,
          created_at: created_at,
          updated_at: created_at,
          uploader_id: this.user?.id,
          file_info: file,
        });
      });
      this.updateLightBox();
    },

    filterMethod(dataArray, filter) {
      return filter && checkIfObject(filter) && Object.values(filter).join('')
        ? (filter?.search
            ? searchInObjectArray(dataArray, filter?.search, [
                // 'path', // also searching from file path?
                'file',
                'name',
                'uploader_id',
              ])
            : dataArray
          ).filter((data) => {
            let res = true;
            // Filter Owner
            if (!data.folder && filter?.owner)
              switch (filter?.owner) {
                case 'me':
                  res = data.uploader_id === this.user?.id;
                  break;
                case 'others':
                  res = data.uploader_id !== this.user?.id;
                  break;
                default:
                  break;
              }
            return res;
          })
        : dataArray.slice();
    },

    // Search
    async fetchFromServer(startRow, count, filter, sortBy, descending) {
      const query = await this.getFiles({
        path: this.path || '/',
        sortBy: sortBy,
        sort: descending ? 'desc' : 'asc',
      });
      const directories = query.data?.directories || [];
      const files = query.data?.files || [];
      // if (this.path !== '/' && this.path !== '') {
      //   // Add directory back navigation
      //   directories.unshift({
      //     id: 'dir-1',
      //     path_query: this.dirBack1(),
      //     folder: true,
      //     name: '..',
      //   });
      // }
      for (const [i, e] of directories.entries()) {
        if (!e.id) e.id = 'dir' + (i + directories.length);
        e.folder = true;
        if (this.isPathOnBrands()) {
          const brand = this.selectedOrgBrands[e.name];
          if (brand?.id && brand?.brand_name) {
            e.brand_id = brand.id;
            e.name = brand.brand_name;
          }
        }
      }

      // Set newData as file
      this.orgFilesOriginal = [...directories, ...files];

      const data = this.filterMethod(this.orgFilesOriginal, filter);

      // handle sortBy
      if (sortBy) {
        const sortFn =
          sortBy === 'file'
            ? descending
              ? (a, b) =>
                  a[sortBy] > b[a.folder ? 'name' : 'file']
                    ? -1
                    : a[a.folder ? 'name' : 'file'] <
                      b[a.folder ? 'name' : 'file']
                    ? 1
                    : 0
              : (a, b) =>
                  a[a.folder ? 'name' : 'file'] > b[a.folder ? 'name' : 'file']
                    ? 1
                    : a[a.folder ? 'name' : 'file'] <
                      b[a.folder ? 'name' : 'file']
                    ? -1
                    : 0
            : descending
            ? (a, b) => parseFloat(b[sortBy]) - parseFloat(a[sortBy])
            : (a, b) => parseFloat(a[sortBy]) - parseFloat(b[sortBy]);
        data.sort(sortFn);
      }

      return count === 0 ? data : data.slice(startRow, startRow + count);
    },

    getRowsNumberCount(filter) {
      if (
        !filter ||
        !(checkIfObject(filter) && Object.values(filter).join(''))
      ) {
        return Object.values(this.orgFilesOriginal).length;
      }
      let count = 0;
      this.filterMethod(this.orgFilesOriginal, filter).forEach((e) => {
        count++;
      });
      return count;
    },

    onRequest(props) {
      const { page, rowsPerPage, sortBy, descending } = props.pagination;
      const filter = props.filter;

      this.tableLoading = true;

      // emulate server
      setTimeout(async () => {
        // update rowsCount with appropriate value
        this.pagination.rowsNumber =
          this.pagination.rowsNumber === 0
            ? 0
            : this.getRowsNumberCount(filter);

        // get all rows if "All" (0) is selected
        const fetchCount =
          rowsPerPage === 0 ? this.pagination.rowsNumber || 0 : rowsPerPage;

        // calculate starting row of data
        const startRow = (page - 1) * rowsPerPage;

        // fetch data from "server"
        const returnedData = await this.fetchFromServer(
          startRow,
          fetchCount,
          filter,
          sortBy,
          descending
        );

        // clear out existing data and add new
        this.orgFiles.splice(0, this.orgFiles.length, ...returnedData);

        // don't forget to update local pagination object
        this.pagination.page = page;
        this.pagination.rowsPerPage = rowsPerPage;
        this.pagination.sortBy = sortBy;
        this.pagination.descending = descending;

        // ...and turn of loading indicator
        this.tableLoading = false;
        this.updateLightBox();
      }, 750);
    },
  },
});
</script>
